@import url(https://fonts.googleapis.com/css?family=Ubuntu:400);
html {
  height: 100%;
  background: linear-gradient(147deg, #5d2673, #000000);
  display: grid;
  box-sizing: border-box;
}

body {
  -webkit-align-self: center;
          align-self: center;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  box-sizing: border-box;
}

#app-title {
  color: #dddddd;
  text-shadow: 0 0 0.75rem black;
  opacity: 0.85;
  margin-top: 0;
  font-size: 2rem;
}

#container {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: "Ubuntu", sans-serif;
  margin: auto;
  margin-bottom: 4rem;
  padding: 0;
  border: 4px solid black;
  border-radius: 10px;
  border-top: 4px solid #333333;
  border-bottom: 4px solid #222222;
  box-shadow: 5px 10px 20px 5px black;
  background: #000000;
  display: grid;
  grid-gap: 0;
  grid-template-columns: repeat(4, 5rem);
  grid-template-rows: repeat(6, 4rem);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

button {
  color: #bbbbbb;
  border: 1px solid #20001f;
  font-size: 1.35rem;
  margin: 0;
  background: black;
  outline: none;
  font-family: "Ubuntu", sans-serif;
}

button:hover {
  transition: all 100ms;
  border: 1px solid #777777;
  text-shadow: 0 0 0.75rem #000000;
}

#display {
  grid-column: 1 / span 4;
  grid-row: 1;
  background: #000000;
  color: #bbbbbb;
  text-align: right;
  padding: 1rem;
  font-size: 1.5rem;
  margin: 0;
  align-self: center;
}

#clear {
  grid-column: 1 / 3;
  grid-row: 2;
  font-size: 1.2rem;
}

#zero {
  grid-column: 1 / 3;
  grid-row: 6;
  border-bottom-left-radius: 8px;
}

#equals {
  grid-column: 4;
  grid-row: 5 / span 6;
  border-bottom-right-radius: 8px;
}

#divide,
#multiply,
#add,
#subtract,
#clear,
#equals {
  background: #000000;
  color: #928458;
  border: 1px solid #2f001f;
}

#divide:active,
#multiply:active,
#add:active,
#subtract:active {
  border: 1px solid #555555;
  box-shadow: 0 0 0.75rem #928458 inset;
}

#divide:hover,
#multiply:hover,
#add:hover,
#subtract:hover {
  transition: all 200ms;
  border: 1px solid #928458;
}

button:active {
  border: 1px solid #555555;
  box-shadow: 0 0 0.5rem #777777 inset;
}

#clear:active {
  box-shadow: 0 0 0.75rem red inset;
}

#clear:hover {
  border: 1px solid darkred;
}

#equals:active {
  box-shadow: 0 0 0.75rem green inset;
}

#equals:hover {
  border: 1px solid green;
}

#divide {
  font-family: sans-serif;
}

/* MEDIA QUERIES */

@media screen and (max-width: 800px) {
  #app-title {
    font-size: 1.75rem;
  }

  #container {
    grid-template-columns: repeat(4, 17vw);
    grid-template-rows: repeat(6, 3rem);
    overflow: hidden;
    margin: 0 auto;
    margin-bottom: 2rem;
  }

  button {
    font-size: 1.15rem;
  }

  #divide,
  #multiply,
  #add,
  #subtract,
  #clear,
  #equals {
    border: 1px solid #2f001f;
  }

  #display {
    font-size: 1.25rem;
  }

  #clear {
    font-size: 1.1rem;
  }
}

@media screen and (max-width: 720px) {
  #container {
    grid-template-columns: repeat(4, 24vw);
    grid-template-rows: repeat(6, 10vh);
  }
}

/* iPad */

@media only screen and (min-width: 768px) and (max-width: 768px) and (min-height: 1024px) and (max-height: 1024px) {
  #app-title {
    font-size: 2rem;
  }

  #container {
    grid-template-columns: repeat(4, 6rem);
    grid-template-rows: repeat(6, 4.5rem);
  }

  button {
    font-size: 1.75rem;
  }

  #display {
    font-size: 1.5rem;
  }

  #clear {
    font-size: 1.5rem;
  }
}

